import {ref} from '@vue/composition-api'
import axios from 'axios'

const config = ref(undefined)
const configError = ref(undefined)

async function fetchConfig () {
  await axios.get(`${process.env.BASE_URL}config.json`).then(
    ({data}) => {
      config.value = data
      configError.value = null
    },
    ({response}) => {
      if (response.status === 404) {
        config.value = null
        configError.value = {
          message: 'Could not find config.json',
        }
      }
    },
  )
}

export default function useConfig () {
  if (!config.value) {
    fetchConfig()
  }
  return {
    config,
    configError,
  }
}
