<template>
  <div id="app" class="min-h-screen bg-gray-50 flex flex-col relative">
    <app-header />
<!--    <header class="absolute left-0 top-0 m-8 text-green-800 tracking-widest uppercase">Melon-Auth V2</header>-->
<!--    <lang-selector class="absolute top-0 right-0 m-5" />-->

    <template v-if="config === undefined">
      <rotating-cube size="40" class="fixed inset-0 m-auto" />
    </template>
    <template v-else-if="config">
      <step-view class="self-center" />
    </template>
    <template v-else>
      <div class="fixed inset-0 text-lg text-gray-600 uppercase flex items-center justify-center">{{ configError.message }}</div>
    </template>

    <app-footer />
  </div>
</template>

<script>
  import {watchEffect} from '@vue/composition-api'

  import LangSelector from '@/components/lang-selector.vue'
  import RotatingCube from '@/components/rotating-cube'
  import AppHeader from '@/components/app-header.vue'
  import AppFooter from '@/components/app-footer.vue'
  import StepView from '@/views/steps/step-view.vue'
  import useConfig from '@/composables/config'

  export default {
    name: 'App',

    components: {
      LangSelector,
      RotatingCube,
      AppHeader,
      AppFooter,
      StepView,
    },

    setup () {
      const {config, configError} = useConfig()

      watchEffect(() => {
        if (config.value?.title) {
          document.title = config.value.title
        }
      })

      return {
        config,
        configError,
      }
    },
  }
</script>
