<template>
  <footer v-if="config.footer_content" class="self-center mt-auto py-8 flex flex-col items-center space-y-4">
    <div v-html="config.footer_content" />
  </footer>
</template>

<script>
  import {computed} from '@vue/composition-api'

  import useConfig from '@/composables/config'

  export default {
    name: 'AppFooter',

    setup () {
      const {config} = useConfig()

      return {
        config: computed(() => config.value ?? {}),
      }
    },
  }
</script>
